import React, {Component} from 'react'
import classes from './Auth.css'
import Button from "../../components/UI/Button/Button";
import Input from "../../components/UI/Input/Input";
import is from 'is_js'

export default class Auth extends Component {

    state = {
        isFormValid: false,
        formControls: {
            email: {
                value: '',
                type: 'email',
                label: 'Email',
                errorMessage: 'Введите корректный email',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    email: true

                }
            },
            password: {
                value: '',
                type: 'password',
                label: 'Пароль',
                errorMessage: 'Введите корректный пароль',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    minLength: 6
                }
            }

        }
    }

    loginHandler = () => {

    }

    registerHandler = () => {

    }

    submitHandler = (event) => {
        event.preventDefault()
    }

//value получаем из нажатия клавиш control.value & валидация из копии стейта
    validateControl(value, validation) {
        //Если валидация не присутствуем значит валидно
        if (!validation) {
            return true
        }
//Изначально валидно
        let isValid = true
//Если есть свойство required и не является пустой строкой и до этого валидация была true
        if (validation.required) {
            isValid = value.trim() !== '' && isValid
        }
//Если email проходит проверку с помощью библиотеки is.js
        if (validation.email) {
            isValid = is.email(value) && isValid
        }
//Если имеется поле проверки на длинну и больше значения и до этого isValid была true(все проверки пройдены)
        if (validation.minLength) {
            isValid = value.length >= validation.minLength && isValid
        }
// Возврашаем пройдена валидация или нет в итоге
        return isValid
    }

    onChangeHandler = (event, controlName) => {
        const formControls = {...this.state.formControls} //Создаем копию стейта
        const control = {...formControls[controlName]}//Забираем каждое значение из массива

        control.value = event.target.value //Записываем в копию нажатие клавиш
        control.touched = true //Меняем значение так как начали что то вводить
        // Проверяем на валидность введенных данных
        control.valid = this.validateControl(control.value, control.validation)
//Перезаписываем копию массива по верхним изменениям
        formControls[controlName] = control
        //Изначально форма валидна
        let isFormValid = true
        // Забираем ключи копии массива и перебираем если ключ имеет valid и он true
        Object.keys(formControls).forEach(name => {
            isFormValid = formControls[name].valid && isFormValid
        })
//Перезаписываем стейт из обработанной копии
        this.setState({
            formControls, isFormValid
        });
    }

//Рендерим инпут со всеми значениями из стейта и в итоге при изменении запускаем onChangeHandler функцию которая меняет стейт
    renderInputs() {
        return Object.keys(this.state.formControls).map((controlName, index) => {
            const control = this.state.formControls[controlName]
            return (
                <Input
                    key={controlName + index}
                    type={control.type}
                    value={control.value}
                    valid={control.valid}
                    touched={control.touched}
                    label={control.label}
                    shouldValidate={!!control.validation}
                    errorMessage={control.errorMessage}
                    onChange={(event) => this.onChangeHandler(event, controlName)}
                />
            )
        })

    }

    render() {
        return (
            <div className={classes.Auth}>
                <div>
                    <h1>Авторизация</h1>
                    {/*Отменяем стандартное поведение формы*/}
                    <form onSubmit={this.submitHandler} className={classes.AuthForm}>
                        {/*Рендерим инпуты которые получаем через Object.key из formControls по ключам*/}
                        {this.renderInputs()}
                        {/*Рендерим кнопки и проверяем на валидность */}
                        <Button
                            type='succes'
                            onClick={this.loginHandler}
                            disabled={!this.state.isFormValid}
                        >
                            Войти
                        </Button>
                        <Button
                            type='primary'
                            onClick={this.registerHandler}
                            disabled={!this.state.isFormValid}
                        >
                            Зарегистрироваться
                        </Button>
                    </form>
                </div>
            </div>
        )
    }
}